@import url('https://fonts.googleapis.com/css?family=PT+Sans|PT+Serif:400,400i');
html,
body {
    background: #F5F5F5;
    font-family: 'PT Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
}

.wordCard {
    border: 0;
    padding: 1rem;
}

.wordCard__word {
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-size: 32px;
}

.wordCard__sentence {
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-style: italic;
}

.fa {
    font-size: 1em;
    margin: 0px 4px;
    width: 12px;
}

.hover {
    cursor: pointer;
}

.word_input {
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-bottom: 2px solid rgb(180, 180, 255);
    border-radius: 0;
    outline: none;
    caret-color: blue;
}

.bordered {
    border: 2px solid black;
}

.as-items .as-item__navigator {
    position: relative;
    left: 0px;
    top: 0px;
}

.as-item {
    margin-bottom: 1.7rem;
}

.as-items a {
    text-decoration: none;
    color: #c33737;
}

.word_input:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
}

.wordCard a {
    text-decoration: none;
    color: #c33737;
}


